.Footer {
  display: flex;
  justify-content: left;
  align-items: center;
  grid-column: 1/-1;
  padding-left: 50px;
  color: white;
  background: black;
  height: 40px;
  margin-top: 40px;
  font-size: 0.8rem; }

.ContactForm {
  grid-area: form;
  display: flex;
  flex-direction: column; }
  .ContactForm-label {
    margin-bottom: 5px;
    font-weight: 600;
    font-size: 12px; }
  .ContactForm-input {
    font-size: 10px;
    border: 1px solid grey;
    border-radius: 7px;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    outline: none;
    padding: 3px 0px 3px 3px;
    border: 1px solid #dddddd; }
    .ContactForm-input:focus {
      box-shadow: 0 0 5px #bdc3c7;
      padding: 3px 0px 3px 3px;
      border: 1px solid #bdc3c7; }
  .ContactForm-textArea {
    height: 80px;
    resize: none;
    margin-bottom: 5px; }
  .ContactForm-textInput {
    margin-bottom: 5px;
    height: 25px; }
  .ContactForm-button {
    padding: 8px;
    width: 90px;
    border-radius: 5px;
    margin-bottom: 5px;
    align-self: center;
    color: white;
    background: black;
    font-weight: 700;
    margin-top: 7px;
    outline: none; }
    .ContactForm-button:focus {
      box-shadow: 0 0 11px #000704; }
    .ContactForm-button:hover {
      box-shadow: 0 0 5px #000704; }
  .ContactForm-message {
    margin: 0 auto;
    font-weight: 600; }

@media screen and (min-width: 1200px) {
  .ContactForm-input {
    min-width: 16vw; } }

@media (min-width: 700px) {
  .ContactForm-label {
    font-size: 14px; }
  .ContactForm-input {
    font-size: 13px; } }

