.infoCard {
  display: grid;
  grid-column: 2;
  grid-template-columns: 1fr auto 1fr; }
  .infoCard-infoContainer {
    border-radius: 7px;
    padding: 15px 40px 15px 40px;
    display: grid;
    grid-column: 2;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    grid-template-columns: repeat(3, auto);
    grid-template-rows: auto;
    grid-template-areas: "info photo form";
    grid-gap: 40px; }
  .infoCard-info {
    white-space: nowrap;
    grid-area: info;
    align-self: center;
    justify-self: stretch; }
    .infoCard-info > * {
      padding: 4px 0px 4px 4px; }
  .infoCard-title {
    white-space: nowrap; }
  .infoCard-photo {
    grid-area: photo;
    width: 7rem;
    width: var(--infoCardPhotoSize);
    height: 7rem;
    height: var(--infoCardPhotoSize);
    border-radius: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    align-self: center;
    justify-self: center; }
  .infoCard-links {
    display: flex;
    flex-direction: column; }

